import React, { useState } from "react";
import 'react-dates/initialize';
import { SingleDatePicker } from "react-dates";
import './custom-datepicker.css';
import styles from './FormDashboardDataItem.module.css';
import bookingIcon from '../../../../assets/icons/booking.png';
import {parseTextToDate} from "./parseTextToDate";

export const FormDashboardDataItem = ({
  state,
  title = '',
  initialData = null,
  setState,
  context = ''
}) => {

  const [isCalendarVisible, setIsCalendarVisible] = useState(false);

  const handleDateChange = (date) => setState({
    ...state,
    checkIn: context === 'start' ? date : state.checkIn,
    checkOut: context === 'end' ? date : state.checkOut,
  });
  const toggleCalendar = () => setIsCalendarVisible(!isCalendarVisible);

  return (
    <div className={styles.container}>
      <p className={styles.title}>{title}</p>
      <div className={styles.wrapper}>
        <img src={bookingIcon} alt={bookingIcon} />
        <div className={styles['data-wrapper']}>
          <button
            className={styles['data-show-text']}
            onClick={() => toggleCalendar()}
          >
            <p>{parseTextToDate(String(initialData)) || ''}</p>
          </button>
          <SingleDatePicker
            date={initialData}
            onDateChange={handleDateChange}
            focused={isCalendarVisible}
            onFocusChange={({ focused }) => setIsCalendarVisible(focused)}
            id="your_unique_start_date_id"
            numberOfMonths={2}
            isOutsideRange={() => false}
            openDirection="up"
            transitionDuration={0}
          />
        </div>
      </div>
    </div>
  );
};


